.companyA {
    margin: 1rem;
    padding: 1rem;
    background-color: #0049B7;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 12px;
    text-align: center;
    color: white;
    list-style-type: none;
    width: 80%;

  }
  
  .companyA h2 {
    font-size: 2rem;
    color: white;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  }
  
  .companyA h3 {
    color: #eccf77;
    margin: 0;
    font-size: 1rem;
  }
  .companyA ul {
    list-style-type: none;
  }