.couponB {
  margin: 2rem;
  
 
  
  align-items: center;
  width: auto;
  background-color: #0049B7;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 12px;
  text-align: center;
  color: white;
  list-style-type: none;
  padding: 1rem;

 
  

}

.couponB p {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  text-align: center;
 
  
}

.couponB h2 {
  
  font-size: 2rem;
  color: white;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.couponB h3 {
  
  color: #eccf77;
  margin: 0;
  font-size: 1rem;
}
