.coupon-date {
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid #f0f008;
    background-color: #4a8bec;
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
   
    margin: 5px;
    
  }

  .coupon-date label {
    margin-top: 1px;
    margin-bottom: 2px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    color: #eccf77;
  }
  
  .coupon-date__month {
    
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .coupon-date__year {
    font-size: 0.75rem;
  }
  
  .coupon-date__day {
    font-size: 1.5rem;
    font-weight: bold;
  }