.customerNav button {
    text-decoration: none;
    color: white;
    font-size: 1.25rem;
  }
  
  .customerNav button:hover,
  .customerNav button:active {
    color: #0099ff;
  }

  .customerNav li {
    align-items: center;
  }

  .header {
    width: 80%;
    height: 5rem;
    display: flex;
    
    justify-content: space-between;
    align-items: center;
    background-color: #0049B7;
    color: white;
    padding: 0 10%;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  }

  /* .header p {

    margin-left: 350px;
    display: flex;
  } */
  
  .header ul {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0; 
    align-items: center;
  }
  
  .header li {
    margin: 0 1rem;
   
  }
  
  .header p {
    text-decoration: none;
    color: white;
    font-size: 1rem;
  }
  
  .header a:hover,
  .header a:active {
    color: #0099ff;
  }
  
  .header button {
    font-size: 1.25rem;
    background-color: #0049B7;
    border: 1px solid white;
    padding: 0.5rem 1.5rem;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    color: white;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    cursor: pointer;

  }
  
  .header button:hover,
  .header button:active {
    background-color: #0099ff;
    border-color: #ffa600;
  }
  