.companypage  {
    color: white;
    margin: 5rem auto;
    width: 80%;
    max-width: 100rem;
    text-align: center;
   background-color: #4a8bec;
    padding: 1rem;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    
  }

  .dropdown button {
    font-size: 1rem;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    background-color: #4a8bec;
    border: 1px solid white;
    padding: 0.5rem 3rem;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    color: white;
    cursor: pointer;
  }

  .dropdown button:hover,
  .dropdown button:active {
    background-color: #0049B7;
    border-color: #4a8bec;
  }
  

  .companypage ul li {
     
   
   margin: 20px;
   list-style-type: none;
   align-items: center;
     
  }

  .dropbtn {
    
    background-color: #0049B7;
    color: white;
    padding: 10px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 16px;
    border:#f9f9f9;
    border-radius: 2px;
    border-width: 1px;
    border-style:solid;
    cursor: pointer;
  }
  
  .dropdown {
    position: relative;
    display: inline-block;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  
  .dropdown-content a:hover {background-color: #527df3; color: white;}
  
  .dropdown:hover .dropdown-content {
    display: block;
  }
  
  .dropdown:hover .dropbtn {
    background-color: #5694f1;
  }